<template>
  <div>
    <transition name="fadeHeight">
      <Banner :banner="banner" v-if="showBanner" />
    </transition>
    
    <div>
      <button @click="priceSort" style="margin: 10px auto">sort by price</button>
      <button @click="priceReverse" style="margin: 10px auto">sort by price reverse</button>
    </div>
    <div>
      <button @click="priceOrigin" style="margin: 10px auto">origin</button>
    </div>
    <transition name="fade">
      <Modal v-if="isModalOpen" :room="room" @onModalOpen="$event => onModalOpen(roomIdx,$event)" />
    </transition>
    <Card @onModalOpen="onModalOpen(idx)" v-for="(room,idx) in roomsFilt" :room="room" :key="idx" :keys="idx" />
  </div>

</template>
<style scoped>

body {
  margin : 0;
}
div {
  box-sizing: border-box;
}
.black-bg {
  width: 100%; height:100%;
  background: rgba(0,0,0,0.5);
  position: fixed; padding: 20px;
}
.white-bg {
  width: 100%; background: white;
  border-radius: 8px;
  padding: 20px;
} 


html {
  text-align: center;
}



/* .작명-enter-from { 애니메이션 동작 전 상태 }
.작명-enter-active { 애니메이션 동작 중 상태, 대부분 transition 이런거 }
.작명-enter-to { 애니메이션 동작 후 상태 } */

.fade-enter-from { opacity: 0;  
transform: translateY(-1000px);}
.fade-enter-active { transition: all 0.26s ease-in-out 0s; }
.fade-enter-to { opacity: 1;transform: translateY(-0px);}

.fade-leave-from { opacity: 1;}
.fade-leave-active { transition: all 0.26s ease-in-out 0s; }
.fade-leave-to { opacity: 0;}

.fadeHeight-enter-from { opacity: 0;  
transform: translateY(-1000px); height: 183px;}
.fadeHeight-enter-active { transition: all 0.26s ease-in-out 0s; }
.fadeHeight-enter-to { opacity: 1;transform: translateY(-0px); height: 0;}

.fadeHeight-leave-from { opacity: 1; height: 183px;}
.fadeHeight-leave-active { transition: all 0.26s ease-in-out 0s; }
.fadeHeight-leave-to { opacity: 0;height: 0;}

</style>
<script>
import Modal from '@/components/lesson/Modal.vue'
import Card from '@/components/lesson/Card.vue'
import Banner from '@/components/lesson/Banner.vue'

export default {
  components: {
    Modal,
    Card,
    Banner,
  },
  name: 'Page1',
  data(){
    return {
      isModalOpen:false,
      roomIdx:0,
      room:{},
      showBanner:true,
      rooms: [{
        id : 0,
        title: "Sinrim station 30 meters away",
        image: "https://codingapple1.github.io/vue/room0.jpg",
        content: "18년 신축공사한 남향 원룸 ☀️, 공기청정기 제공",
        price: 340000
        },
        {
        id : 1,
        title: "Changdong Aurora Bedroom(Queen-size)",
        image: "https://codingapple1.github.io/vue/room1.jpg",
        content: "침실만 따로 있는 공용 셰어하우스입니다. 최대 2인 가능",
        price: 450000
        },
        {
        id : 2,
        title: "Geumsan Apartment Flat",
        image: "https://codingapple1.github.io/vue/room2.jpg",
        content: "금산오거리 역세권 아파트입니다. 애완동물 불가능 ?",
        price: 780000
        },
        {
        id : 3,
        title: "Double styled beds Studio Apt",
        image: "https://codingapple1.github.io/vue/room3.jpg",
        content: "무암동인근 2인용 원룸입니다. 전세 전환가능",
        price: 550000
        },
        {
        id : 4,
        title: "MyeongIl Apartment flat",
        image: "https://codingapple1.github.io/vue/room4.jpg",
        content: "탄천동 아파트 월세, 남향, 역 5분거리, 허위매물아님",
        price: 680000
        },
        {
        id : 5,
        title: "Banziha One Room",
        image: "https://codingapple1.github.io/vue/room5.jpg",
        content: "반지하 원룸입니다. 비올 때 물가끔 새는거 빼면 좋아요",
        price: 370000
      }],
      roomsFilt: [],
      banner:{
        discount:5,
        price: 370000
      }
    }
  },
  methods:{
    onModalOpen(idx,data){
      console.log('data = ', data)
      if(this.isModalOpen === true) {
        this.resultPrice = 0;
        this.month = 0;
        } else {
          this.roomIdx = idx
          this.room = this.rooms[idx]
        }
        this.isModalOpen = !this.isModalOpen
    },
    priceSort(){
      this.roomsFilt = [...this.rooms].sort((a,b)=>{
        return a.price - b.price
      })
    },
    priceReverse(){
      this.roomsFilt = [...this.rooms].sort((a,b)=>{
        return b.price - a.price
      })
    },
    priceOrigin(){
      this.roomsFilt = this.rooms
    },
  },
  created(){

  },
  mounted(){
    console.log(window)
    this.roomsFilt = this.rooms
    const itv = setInterval(()=>{
      if(this.banner.discount > 1) this.banner.discount--
      else {
        clearInterval(itv)
        this.showBanner = false
      }
    },1000)
  },
  unmounted(){

  },
}
</script>

